.rdrCalendarWrapper {
    box-sizing: border-box;
    background: transparent;
    display: inline-flex;
    flex-direction: column;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .rdrDateDisplay{
    display: flex;
    justify-content: space-between;
  }
  
  .rdrDateDisplayItem{
    flex: 1 1;
    width: 0;
    text-align: center;
    color: inherit;
  }
  
  .rdrDateDisplayItem + .rdrDateDisplayItem{
      margin-left: 0.833em;
    }
  
  .rdrDateDisplayItem input{
      text-align: inherit
    }
  
  .rdrDateDisplayItem input:disabled{
        cursor: default;
      }
  
//   .rdrDateDisplayItemActive{}
  
  .rdrMonthAndYearWrapper {
    box-sizing: inherit;
    display: flex;
    justify-content: space-between;
  }
  
  .rdrMonthAndYearPickers{
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
//   .rdrMonthPicker{}
  
//   .rdrYearPicker{}
  
  .rdrNextPrevButton {
    box-sizing: inherit;
    cursor: pointer;
    outline: none;
  }
  
//   .rdrPprevButton {}
  
//   .rdrNextButton {}
  
  .rdrMonths{
    display: flex;
  }
  
  .rdrMonthsVertical{
    flex-direction: column;
  }
  
  .rdrMonthsHorizontal > div > div > div{
    display: flex;
    flex-direction: row;
  }
  
  // .rdrMonth{
  //   width: 27.667em;
  // }
  
  .rdrWeekDays{
    display: flex;
  }
  
  .rdrWeekDay {
    flex-basis: calc(100% / 7);
    box-sizing: inherit;
    text-align: center;
  }
  
  .rdrDays{
    display: flex;
    flex-wrap: wrap;
    color: $textcolor !important;
  }
  
  
  .rdrInfiniteMonths{
    overflow: auto;
  }
  
  .rdrDateRangeWrapper{
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .rdrDateInput {
    position: relative;
  }
  
  .rdrDateInput input {
      outline: none;
    }
  
  .rdrDateInput .rdrWarning {
      position: absolute;
      font-size: var(--fs-body);
      line-height: 1.6em;
      top: 0;
      right: .25em;
    }
  
  .rdrDay {
    box-sizing: inherit;
    width: calc(100% / 7);
    position: relative;
    font: inherit;
    cursor: pointer;
  }
  
  .rdrDayNumber {
    display: block;
    position: relative;
  }
  
  .rdrDayNumber span{
      color: $textcolor
    }
  
  .rdrDayDisabled {
    cursor: not-allowed;
  }
  
  @supports (-ms-ime-align: auto) {
    .rdrDay {
      flex-basis: 14.285% !important;
    }
  }
  
  .rdrSelected, .rdrInRange, .rdrStartEdge, .rdrEndEdge{
    pointer-events: none;
  }
  

  .rdrDayStartPreview, .rdrDayInPreview, .rdrDayEndPreview{
    pointer-events: none;
  }
  
  .rdrDateRangePickerWrapper{
    display: inline-flex;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  
  .rdrStaticRanges{
    display: flex;
    flex-direction: column;
  }
  
  .rdrStaticRange{
    font-size: var(--fs-body);
  }
  
  .rdrInputRange{
    display: flex;
  }
  
  